@keyframes slideUpAnimation {
  0% {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
  90% {
    opacity: 1;
  }
  100% {
    transform: translate3d(0, 0, 0);
  }
}
*,
*:after,
*:before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  padding: 0;
  font-family: TheSeasons, Arial, Helvetica, sans-serif;
  background-size: 100px;
  background-repeat: repeat;
  color: #395b76;
}

section {
  padding: 0 40px !important;
}

hr {
  max-width: 100px;
  margin: 30px auto;
  color: inherit;
  border: 0;
  border-top: 1px #395b76 solid;
  opacity: 0.25;
}
hr.big {
  margin: 70px auto;
}

.sans-serif-text {
  font-family: Verona, Arial, Helvetica, sans-serif;
}

.reveal {
  clip-path: inset(0 100% 0 0);
  transition: clip-path 0.5s;
  transition-timing-function: cubic-bezier(0.85, 0, 0.15, 1);
  overflow: hidden;
}

.animate-revealed {
  clip-path: inset(0);
}

a:hover,
a:focus {
  text-decoration: none;
  color: inherit;
}

b {
  font-weight: 600;
}

@media (min-width: 1400px) {
  .container {
    max-width: 1650px;
  }
}
a,
a[href^=tel],
a[href^=mailto] {
  color: inherit;
  /* Inherit text color of parent element. */
  text-decoration: none;
  /* Remove underline. */
}

.icon:before {
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  margin-right: 5px;
}

.main-content {
  padding: 40px;
  background-image: url("../images/tropical.webp");
  background-size: cover;
}
.main-content .main-content-wrapper,
.main-content .main {
  border-top-left-radius: 500px;
  border-top-right-radius: 500px;
}
.main-content .main-content-wrapper {
  position: relative;
  background-color: rgba(253, 250, 245, 0.8);
  backdrop-filter: blur(5px);
  -webkit-backface-visibility: hidden;
